<template>
  <div class="hello">
	<h2>Oraganization</h2>
   <div>
    <div class="input-group md-form form-sm form-2 pl-0">
      <input v-model="search" class="form-control my-0 py-1 red-border" type="text" placeholder="Search" aria-label="Search">
    </div>
  </div>
    <b-container class="bv-example-row" >
    <div>
      <b-row>
    <b-col v-for="todo in filterdtodos" :key="todo.id" cols="4">
    <b-card-group deck >
    <b-card header-tag="header" footer-tag="footer">
      <template v-slot:header>
        <h6 class="mb-0">{{todo.name}}</h6>
      </template>
      <b-card-text>{{todo.domain_names}}</b-card-text>
      <template v-slot:footer>
        <em>{{todo.external_id}}</em>
      </template>
    </b-card>
  </b-card-group>
  </b-col>
  </b-row>
</div>
</b-container>
	
  </div>
</template>

<script>
import organizations from './json/organizations.json'

export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data () {
    return {
			todos: organizations,
      search:''
    }
  },
  computed:{
    filterdtodos:function(){
      return this.todos.filter((todo)=>{
       // return todo.name.match(this.search);
        return this.search.toLowerCase().split(' ').every(v => todo.name.toLowerCase().includes(v));
        
    //console.log(todo);
      })
    }
  }
}
</script>

